body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

ul {
  list-style-type: none;
}
/* app  */
.app {
  display: grid;
  min-height: 100vh;
  width: 100%;
  /* overflow: auto; */
  grid-template-rows: 150px 1fr;
}
/* nav */
.nav-container {
  background-color: yellow;
  padding: 1rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.nav-title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
  background-color: white;
  padding: 1rem;
  border-radius: 10px;
  font-style: italic;
}

.logo {
  width: 50px;
  height: 50px;
  border-radius: 100%;
}

.faces {
  display: flex;
  gap: 1rem;
}
.character {
  width: 80px;
  height: 80px;
  border-radius: 100%;
  padding: 5px;
}

.notFound {
  background-color: blue;
}
.found {
  background-color: red;
  opacity: 0.4;
}

/* gameboard  */
.gb {
  display: flex;
  justify-content: center;
  position: relative;
  padding: 2rem;
}

.img {
  width: 100%;
  height: 100%;
  border-radius: 1rem;
}

.tl {
  position: absolute;
  background-color: yellow;
  padding: 5px 10px;
  display: grid;
  gap: 5px;
}

.targeting {
  width: 50px;
  height: 50px;
  border: 2px solid blue;
}

.announce {
  position: fixed;
  color: blue;
  background-color: white;
  border-radius: 1rem;
  font-size: 40px;
  padding: 0.5rem 1rem;
  top: 30%;
  right: 35%;
  z-index: 1;
}

.tag {
  font-size: 0.8rem;
}